



































































































































































.split-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;

  .split-pane {
    position: absolute;
    top: 0;
    bottom: 0;

    &.left-pane {
      left: 0;
    }

    &.right-pane {
      right: 0;
    }
  }
}

.split-trigger {
  &-con {
    position: absolute;
    z-index: 10;
    top: 50%;
    width: 6px;
    height: 100%;
    background: #f8f8f9;
    border-top: none;
    border-bottom: none;
    cursor: col-resize;
    transform: translate(-50%, -50%);
    &__bar {
      position: absolute;
      left: 1px;
      top: 50%;
      height: 32px;
      overflow: hidden;
      transform: translateY(-50%);
    }
    .bar-item {
      width: 4px;
      height: 1px;
      background: rgba(23, 35, 61, .25);
      float: left;
      margin-top: 3px;
    }
  }
}
