.split-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.split-wrapper .split-pane {
  position: absolute;
  top: 0;
  bottom: 0;
}
.split-wrapper .split-pane.left-pane {
  left: 0;
}
.split-wrapper .split-pane.right-pane {
  right: 0;
}
.split-trigger-con {
  position: absolute;
  z-index: 10;
  top: 50%;
  width: 6px;
  height: 100%;
  background: #f8f8f9;
  border-top: none;
  border-bottom: none;
  cursor: col-resize;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.split-trigger-con__bar {
  position: absolute;
  left: 1px;
  top: 50%;
  height: 32px;
  overflow: hidden;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.split-trigger-con .bar-item {
  width: 4px;
  height: 1px;
  background: rgba(23, 35, 61, 0.25);
  float: left;
  margin-top: 3px;
}
