.code-editor__header {
  width: 100%;
  height: 50px;
  padding: 0 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 1000;
  position: fixed;
  top: 0;
  background-color: #fff;
  -webkit-box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
          box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
  font-size: 14px;
}
.code-editor__logo {
  float: left;
  height: 50px;
  line-height: 48px;
}
.code-editor__logo img {
  height: 32px;
  vertical-align: middle;
}
.code-editor__header-menu {
  float: left;
  height: 50px;
  line-height: 48px;
  margin-left: 32px;
}
.code-editor-icon {
  display: inline-block;
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  text-rendering: auto;
  line-height: 1;
  vertical-align: middle;
}
.code-editor__ask {
  float: right;
  height: 50px;
  line-height: 45px;
}
.header-menu-button {
  color: #515a6e;
  display: inline-block;
  margin-bottom: 0;
  font-weight: 400;
  text-align: center;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 4px;
  -webkit-transition: color 0.2s linear, background-color 0.2s linear, border 0.2s linear, -webkit-box-shadow 0.2s linear;
  transition: color 0.2s linear, background-color 0.2s linear, border 0.2s linear, -webkit-box-shadow 0.2s linear;
  transition: color 0.2s linear, background-color 0.2s linear, border 0.2s linear, box-shadow 0.2s linear;
  transition: color 0.2s linear, background-color 0.2s linear, border 0.2s linear, box-shadow 0.2s linear, -webkit-box-shadow 0.2s linear;
  background-color: transparent;
  vertical-align: middle;
  line-height: 1.5;
  padding: 6px 15px;
  font-size: 14px;
}
.header-menu-button span {
  vertical-align: middle;
}
.header-menu-button:active,
.header-menu-button:hover,
.header-menu-button:focus {
  outline: 0;
  color: #2b85e4;
  text-decoration: none;
  background-color: #fff;
  border-color: transparent;
}
