.code-editor__codemirror {
  height: 100%;
  line-height: 1.5;
}
.code-editor__codemirror .CodeMirror {
  height: 100%;
  font-weight: 400;
  font-size: 12px;
  color: #525252;
}
.code-editor__codemirror .CodeMirror-code {
  font-family: Consolas, Menlo, Courier, monospace;
}
.code-editor__codemirror .CodeMirror-guttermarker-subtle {
  font-family: monospace;
}
.code-editor__codemirror .cm-s-default .cm-tag {
  color: #3e76f6;
}
.code-editor__codemirror .cm-s-default .cm-attribute {
  color: #e96900;
}
.code-editor__codemirror .cm-s-default .cm-string {
  color: #31a36f;
}
.code-editor__codemirror .cm-s-default .cm-meta,
.code-editor__codemirror .cm-s-default .cm-qualifier {
  color: #e96900;
}
.code-editor__codemirror .cm-s-default .cm-property {
  color: #9b1dea;
}
.code-editor__codemirror .cm-s-default .cm-atom {
  color: #42b983;
}
.code-editor__codemirror .cm-s-default .cm-number {
  color: #fc1e70;
}
.code-editor__codemirror .cm-s-default .cm-keyword {
  color: #e96900;
}
.code-editor__codemirror .cm-s-default .cm-variable {
  color: #3e76f6;
}
.code-editor__codemirror .cm-s-default .cm-def,
.code-editor__codemirror .cm-s-default .cm-type,
.code-editor__codemirror .cm-s-default .cm-variable-2,
.code-editor__codemirror .cm-s-default .cm-variable-3 {
  color: #2db7f5;
}
.code-editor__codemirror .cm-s-default .cm-bracket.cm-tag {
  color: #525252;
}
.code-editor__codemirror .cm-s-default .cm-comment {
  color: #b3b3b3;
}
.code-editor__codemirror .CodeMirror-activeline-background {
  background: #f8f8f9;
}
